
.custom-picSwipe{
  margin-top:50%;
  text-align:center;
}
@component-namespace page {
@component swipe {
@descendent desc {
      text-align: center;
      color: #666;
      margin-bottom: 5px;
}
.mint-swipe {
      height: 200px;
      color: #fff;
      font-size: 30px;
      text-align: center;
      margin-bottom: 20px;
}
.mint-swipe-item {
      line-height: 200px;
}
.slide1 {
      background-color: yellow;
      color: #fff;
}
.slide2 {
      background-color: #ffd705;
      color: #000;
}
.slide3 {
      background-color: #ff2d4b;
      color: #fff;
}
}
}



