  /* 评论样式 */
.comment {
    line-height:50px;
    height:50px;
    color:#898989;
    font-size:14px;
    padding-left:7.5%;
    margin-left:1%;
    background:url(../assets/comment.png) no-repeat left center;
}
  /* 设置css stack 堆叠效果 */
.stack:last-of-type {
    margin-right:0;
}
.stack{
    z-index:10
}
.stack img{
    max-width:100%;
    height:auto;
    vertical-align:bottom;
    border:4px solid #eaeaea;
    border-radius:3px;
    box-sizing:border-box;
    box-shadow:0 1px 4px rgba(0,0,0,0.4);
}
.stack.rotated-left:before{
        -webkit-transform-origin: bottom left;
    transform-origin: bottom left;
    -webkit-transform: rotate(-3deg);
    transform: rotate(-3deg);
}
.stack.rotated-left:after{
    -webkit-transform-origin: bottom left;
    transform-origin: bottom left;
    -webkit-transform: rotate(-6deg);
    transform: rotate(-6deg);
}
.stack:before{
     top:4px;
     z-index:-10;
}
.stack:after{
    top:8px;
    z-index:-20;
}
.stack:before,
  .stack:after {
    content:'';
    border-radius:3px;
    width:100%;
    height:100%;
    position:absolute;
    border:4px solid #eaeaea;
    left:0;
    box-sizing: border-box;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    transition: 0.3s all ease-out;
}
.custom-homework-content .custom-badge{
     position: absolute;
     right:5px;
     top:114px;
}
.custom-homework-content .custom-content{
     height: 82%;
     max-width:190px;
     padding-left:0.8em;
}
.custom-homework-content .custom-label{
      min-width: 70px;
      max-width:87px;
      word-break:break-all; 
　　  word-wrap:break-word;
}
.custom-homework-content .custom-wrap{
        position: relative;
        background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 50%);
        background-size: 120% 1px;
        background-repeat: no-repeat;
        background-position: top left;
        background-origin: content-box;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        box-sizing: border-box;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        font-size: 16px;
        line-height: 1;
        min-height: inherit;
        height:280px;
        overflow: hidden;
        padding: 0 10px;
        width: 100%;
}
.custom-homework-content{
        background-color: #fff;
        box-sizing: border-box;
        color: inherit;
        min-height: 48px;
        display: block;
        overflow: hidden;
        position: relative;
        text-decoration: none;
}



