
.custom-notice-content .custom-badge{
     position: absolute;
     right:5px;
     top:114px;
     color:#888;
}
@media screen and (max-width: 400px) {
.custom-notice-content .custom-badge{
        position: absolute;
        right:5px;
        top:114px;
        color:#888;
        font-size:15px;
}
.custom-notice-content .custom-content{
        height: 100px;
        text-indent:2em;
        font-size:15px;
}
.custom-notice-content .custom-label{
          min-width: 70px;
          word-break:break-all; 
    　　  word-wrap:break-word;
          max-width:87px;
          font-size:17px;
}
}
@media screen and (max-width:320px){
.custom-notice-content .custom-badge{
        position: absolute;
        right:5px;
        top:114px;
        color:#888;
        font-size:13px;
}
.custom-notice-content .custom-content{
        height: 100px;
        text-indent:2em;
        font-size:13px;
}
.custom-notice-content .custom-label{
          min-width: 70px;
          word-break:break-all; 
    　　  word-wrap:break-word;
          max-width:87px;
          font-size:15px;
}
}
.custom-notice-content .custom-content{
     height: 100px;
     text-indent:2em;
}
.custom-notice-content .custom-label{
      min-width: 70px;
      word-break:break-all; 
　　  word-wrap:break-word;
      max-width:87px;
}
.custom-notice-content .custom-wrap{
        position: relative;
        background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 50%);
        background-size: 120% 1px;
        background-repeat: no-repeat;
        background-position: top left;
        background-origin: content-box;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        box-sizing: border-box;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        font-size: 16px;
        line-height: 1;
        min-height: inherit;
        overflow: hidden;
        padding: 0 10px;
        width: 100%;
}
.custom-notice-content{
        background-color: #fff;
        box-sizing: border-box;
        color: inherit;
        min-height: 48px;
        display: block;
        overflow: hidden;
        position: relative;
        text-decoration: none;
}



