
div.custom-radio div.mint-cell-title {
  min-width: 100%;
}
.mint-radiolist-label {
  padding: 0!important;
}
.yearStyle {
  text-align: center;
  margin-top: 12px;
}
.normal {
  color: #337ab0;
  margin: 0;
}




/*cell 自定义样式*/
li.page-infinite-listitem div.mint-cell-title {
  min-width: 70px;
}
.custom-homework div.mint-cell-title,
.custom-notice div.mint-cell-title {
  min-width: 70px;
}
.mint-cell-value {
  width: 80%;
  white-space:nowrap;
  text-overflow:ellipsis;overflow:hidden;
}




/*底部栏动画*/
.mint-tabbar.is-fixed {
  top: 100%;
}
.diaphaneity {
  width: 100%;
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
  height: 30px;
  position: fixed;
  z-index: 1;
  right: 0;
  bottom: 0;
  left: 0;
}




/*无线滚动样式*/
.mint-tab-container {
  /*top:-35px;*/
  /*bottom: 35px;*/
}
.page-infinite-list {
  margin: 0;
}
.page-infinite-loading div {
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
}
.page-infinite-wrapper {
  margin-top: -1px;
  /*margin-bottom: 55*/
  overflow: scroll;
}
.page-infinite-loading {
  text-align: center;
  height: 40px;
  line-height: 25px;
  margin: 0;
}
.page-infinite-listitem {
  width: 100%;
  height: 50px;
  line-height: 50px;
}
.page-infinite-desc,
.page-infinite-listitem {
  text-align: center;
  border-bottom: 1px solid #eee;
  margin: 0;
}
.breadcrumb>.active {
  color: #777;
}
.breadcrumb>li+li:before {
  padding: 0 5px;
  box-sizing: border-box;
  color: #ccc;
  content: "/\00a0";
}
.breadcrumb>li>a {
  color: #337ab7;
  text-decoration: none;
}
.breadcrumb>li {
  display: inline-block;
}
.breadcrumb {
  margin-top: 0;
  padding: 8px 15px;
  margin-bottom: 0;
  list-style: none;
  background-color: #f5f5f5;
  border-radius: 4px;
}
.page-tabbar {
  overflow: hidden;
  /*height: 100vh;*/
}
.page-wrap {
  overflow: auto;
  height: 100%;
  padding-bottom: 100px;
}



